function SidebarFooterAccountPopover() {
  return (
    <Stack direction="column">
      <Typography variant="body2" mx={2} mt={1}>
        Tài Khoản
      </Typography>
      <MenuList>
        <MenuItem
          component="button"
          sx={{ justifyContent: "flex-start", width: "100%", columnGap: 2 }}
        >
          <ListItemIcon>
            <Avatar
              sx={{ width: 32, height: 32 }}
              src="https://avatars.githubusercontent.com/u/19550456"
            />
          </ListItemIcon>
          <ListItemText primary="Chỉnh Sửa Tài Khoản" />
        </MenuItem>
        <MenuItem
          component="button"
          sx={{ justifyContent: "flex-start", width: "100%", columnGap: 2 }}
        >
          <ListItemIcon>
            <Avatar
              sx={{ width: 32, height: 32 }}
              src="https://avatars.githubusercontent.com/u/19550456"
            />
          </ListItemIcon>
          <ListItemText primary="Đổi Mật Khẩu" />
        </MenuItem>
        <MenuItem
          component="button"
          sx={{ justifyContent: "flex-start", width: "100%", columnGap: 2 }}
        >
          <ListItemIcon>
            <Avatar
              sx={{ width: 32, height: 32 }}
              src="https://avatars.githubusercontent.com/u/19550456"
            />
          </ListItemIcon>
          <ListItemText primary="Đăng Xuất" />
        </MenuItem>
      </MenuList>
      <Divider />
      <AccountPopoverFooter>
        <SignOutButton />
      </AccountPopoverFooter>
    </Stack>
  );
}

const createPreviewComponent = (mini) => {
  return function PreviewComponent(props) {
    return <AccountSidebarPreview {...props} mini={mini} />;
  };
};

function SidebarFooterAccount({ mini }) {
  const PreviewComponent = useMemo(() => createPreviewComponent(mini), [mini]);
  return (
    <Account
      slots={{
        preview: PreviewComponent,
        popoverContent: SidebarFooterAccountPopover,
      }}
      slotProps={{
        popover: {
          transformOrigin: { horizontal: "left", vertical: "bottom" },
          anchorOrigin: { horizontal: "right", vertical: "bottom" },
          disableAutoFocus: true,
          slotProps: {
            paper: {
              elevation: 0,
              sx: {
                overflow: "visible",
                filter: (theme) =>
                  `drop-shadow(0px 2px 8px ${theme.palette.mode === "dark" ? "rgba(255,255,255,0.10)" : "rgba(0,0,0,0.32)"})`,
                mt: 1,
                "&::before": {
                  content: '""',
                  display: "block",
                  position: "absolute",
                  bottom: 10,
                  left: 0,
                  width: 10,
                  height: 10,
                  bgcolor: "background.paper",
                  transform: "translate(-50%, -50%) rotate(45deg)",
                  zIndex: 0,
                },
              },
            },
          },
        },
      }}
    />
  );
}

const demoSession = {
  user: {
    name: "Bharat Kashyap",
    email: "bharatkashyap@outlook.com",
    image: "https://avatars.githubusercontent.com/u/19550456",
  },
};

export default function DashboardLayoutAccountSidebar({ window }) {
  const [pathname, setPathname] = useState("/dashboard");
  const router = useMemo(
    () => ({
      pathname,
      searchParams: new URLSearchParams(),
      navigate: (path) => setPathname(String(path)),
    }),
    [pathname]
  );

  const demoWindow = window !== undefined ? window() : undefined;

  const [session, setSession] = useState(demoSession);
  const authentication = useMemo(
    () => ({
      signIn: () => setSession(demoSession),
      signOut: () => setSession(null),
    }),
    []
  );

  return (
    <AppProvider
      navigation={NAVIGATION}
      router={router}
      theme={demoTheme}
      window={demoWindow}
      authentication={authentication}
      session={session}
    >
      <DashboardLayout
        slots={{
          toolbarAccount: () => null,
          sidebarFooter: SidebarFooterAccount,
        }}
      >
        <DemoPageContent pathname={pathname} />
      </DashboardLayout>
    </AppProvider>
  );
}
